.backdrop {
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, .5);
    z-index: 5;
    transition: transform .1s ease-out;
}

.t1 {
    transform: translateX(100%);
}

.t2 {
    transform: translateX(0);
}

@media only screen and (min-width: 801px) {
    .backdrop {
        display: none;
    }
}