.toggle-button {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button:hover .v-line {
    background: var(--violet);
}

.toggle-button:hover .vb-line {
    background: linear-gradient(90deg, var(--violet), var(--blue));
}

.toggle-button:hover .b-line {
    background: var(--blue);
}

.toggle-button-line {
    width: 30px;
    height: 3px;
    /* background: white; */
}

.v-line {
    background: #000;
}

.vb-line {
    background: #000;
}

.b-line {
    background: #000;
}

.toggle-white {
    background: black;
}

.toggle-violet {
    background: var(--violet);
}