.nav-right {
    flex: 1;
}

header {
    margin: 1em 20px 1em 1.25em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav {
    width: 40%;
}

nav ul {
    /* background: red; */
    display: flex;
    justify-content: space-between;
    color: #474444;
    white-space: nowrap;
}

nav ul li {
    padding: 10px;
}

nav ul li:hover {
    color: var(--violet);
    /* font-weight: bold; */
    /* transform: translateY(-5px) */
    border-bottom: 1px solid var(--violet)
}

.nav-logo {
    width: 150px;
}

/* .hide-desktop {
    display: none;
} */

@media only screen and (max-width: 1366px) {
    nav {
        width: 50%;
    }
}

@media only screen and (max-width: 1080px) {
    nav {
        width: 60%;
    }
}

/* .exit-btn {
