.side-drawer {
    height: 100vh;
    background: white;
    width: 60%;
    max-width: 300px;
    z-index: 200;
    transform: translateX(100%);
    transition: transform .2s ease-out;
}

@media (min-width: 801px) {
    .side-drawer {
        display: none;
    }
}

.side-drawer.open {
    transform: translateX(0);
    box-shadow: 1px 0 5px 1px rgba(0, 0, 0, .5);
}

.side-drawer ul {
    margin: 0;
    padding: 0;
    /* background: red; */
    list-style: none;
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
}

.side-drawer li {
    padding: 20px;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: var(--violet);
    /* border-bottom: 1px solid var(--violet); */
}

.exit-btn {
    display: block;
    margin-bottom: 1em;
    margin-top: -1.3em;
    text-align: right;
    padding: 0 1.4em;
}
  
.exit-btn img {
    width: 15px;
    cursor: pointer;
}