.App {
  text-align: center;
}

.hide {
  display: none;
}

.show {
  display: block;
}

.container {
  /* text-align: center; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 80vh;
  align-items: center;
  padding: .8em 1.2em;
}

.sticky {
  position: fixed;
  top: 0;
  right: 0;
}

.bold {
  font-weight: bold;
}

.centered {
  text-align: center;
}

.no-margin-top {
  margin-top: 0;
}

.padded-container {
  padding: .8em 1.2em;
}

.pointer {
  cursor: pointer;
}

.pulse {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 0 0 rgba(126,67,153, 0.4);
  animation: pulse 2s infinite;
  position: fixed;
}

.pulse:hover {
  animation: none;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(126,67,153, 0.4);
  }
  70% {
      box-shadow: 0 0 0 10px rgba(126,67,153, 0);
  }
  100% {
      box-shadow: 0 0 0 0 rgba(126,67,153, 0);
  }
}