.logo-home {
    width: 60%;
    animation: move 2s infinite alternate;
}

@keyframes move {
    0% {
      transform: translateY(0px);
    }

    100% {
      transform: translateY(10px);
    }
}

.head {
    font-size: 4em;
}

.subhead {
    font-size: 2em;
}

@media only screen and (max-width: 1366px) {
    .head {
        font-size: 3em;
    }
    .subhead {
        font-size: 1.5em;
    }
}

@media only screen and (max-width: 1080px) {
    .head {
        font-size: 2em;
    }
    .subhead {
        font-size: 1em;
    }
}

