.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.form {
    background: white;
    width: 500px;
    box-shadow: 0 5px 25px 25px rgba(0, 0, 0, .2);
    padding: 40px 40px 30px 40px;
    border: 1px solid rgba(0, 0, 0, .2);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form label {
    display: inline-block;
    margin-bottom: 2px;
}

.form input {
    padding: 10px 10px;
    /* margin-bottom: 1.4em; */
    width: 100%;
    box-shadow: none;
    outline: none;
    /* border: 1px solid black;
    border-radius: 5px; */
    border: 1px solid #dfdfdf;
    border-radius: 5px;
    background: none;
    font-size: 1.2em;
}

.form img {
    width: 70%;
}

.form button {
    border-bottom: none;
    cursor: pointer;
    /* background: #f7497d; */
    background: #000;
    color: #fff;
    margin-bottom: 0;
    border-radius: 30px;
    transition: all .2s ease-in-out;
}

.form button:active {
    box-shadow: 0 1px #666;
    transform: translateY(1px);
}

.form button:hover {
    background: var(--violet);
    /* border-radius: 10px; */
}

/* .form input[type="submit"] {
    border-bottom: none;
    cursor: pointer;
    background: #f7497d;
    color: #fff;
    margin-bottom: 0;
} */

.form input:focus {
    border: 1px solid var(--violet);
}

.form input:focus + label {
    color: var(--violet);
}

@media (max-width: 550px) {
    .form {
        width: 300px;
    }

    .form .forgot {
        text-align: center;
    }

    .form h1 {
        font-size: 3rem;
    }
}

.form label {
    position: relative;
    top: -32px;
    left: 0;
    /* color: #999; */
    color: black;
    pointer-events: none;
    transform: .5s;
    transition:all .2s linear;
}

.form input:focus ~ label,
.form input:valid ~ label {
    top: -12px;
    left: 0;
    /* color: rgb(106, 54, 116); */
    color: var(--violet);
    font-size: .7em;
    font-weight: bold;
}







.button-wrapper {
    /* font-size: 21px; */
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: white;
    width: 100%;
    height: 100px;
    /* position: absolute;
    top: calc(50% - 70px);
    left: 0; */
    /* background: green; */
    /* position: absolute; */
    /* padding: 20px 0px; */
    overflow: visible;
}

.submit {
    user-select: none;
    z-index: 2;
    width: 300px;
    height: 3em;
    background: linear-gradient(to right, var(--violet) 0%, var(--blue) 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 200px;
    /* text-align: center; */
    cursor: pointer;
    color: #fff;
    position: absolute;
    /* line-height: 440%; */
    transition: all 0.7s ease, line-height 0s ease, color 0.4s ease, filter 0.2s ease;
}

.submit:before {
    content: "";
    z-index: -2;
    width: 100%;
    height: 100%;
    background: none;
    display: block;
    background: none;
    /* border-radius: 200px;
    opacity: 0.8; */
    transform: scale(0.9) translateY(15px);
    /* filter: blur(20px); */
    transition: all 0.2s ease;
}

.submit:after {
    content: "";
    z-index: -4;
    width: 100%;
    height: 100%;
    background: none;
    display: block;
    border-radius: 200px;
    /* opacity: 0; */
    /* transform: scale(1); */
}

.checked .submit {
    width: 3em;
    border-radius: 50%;
    color: rgba(255,255,255,0);
    /* background: linear-gradient(to right, #ff7b6e 0%,#ff7b6e 100%); */
    /* background: var(--violet); */
    animation: submit-anim 4s cubic-bezier(0.39, 0.575, 0.565, 1);
}

@keyframes submit-anim {
    0% {
        transform: scale(1);
    }
    5%, 95% {
        transform: scale(0.9);
    }
    100% {
        transform: scale(1);
    }
}

.loader {
    width: 95px;
    height: 95px;
    border-radius: 50%;
    opacity: 1;
    z-index: 1;
    position: absolute;
    transform: scale(.2);
}

.loader-wrapper, .loader {
    /* position: absolute; */
    background: red;
}

.checked .loader-wrapper {
    animation: wrapper-loader-anim 4.2s linear;
    /* width: 95px;
    height: 95px; */
    transform: scale(0);
    /* margin-left: -50px; */
}

/* .orange {
    background-color: #ff7b6e;
    transform: scale(0.2) rotate(30deg) translate(0px);
} */

.loader-violet {
    background-color: var(--violet);
}

.loader-blue {
    background-color: var(--blue);
}

.checked .loader {
    opacity: 1;
}

.checked .loader-violet {
    animation: loader-anim-violet 4s 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.checked .loader-blue {
    animation: loader-anim-blue 4s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.checked .orange {
    animation: loader-anim-orange 4s 0.7s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.check-wrapper {
    position: absolute;
    /* width: 95px;
    height: 95px; */
    z-index: -1;
    opacity: 1;
}

.checkmark {
    display: inline-block;
    transform: rotate(45deg);
    height: 18px;
    width: 10px;
    border-bottom: 4px solid #fff;
    border-right: 4px solid #fff;
  }

.checked .checkmark {
    animation: checkmark 4s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.checked .check-wrapper {
    z-index: 3;
}

@keyframes loader-anim-violet {
    0% {
        transform: scale(0.2) rotate(0deg) translate(170px, 0px);
    }
    95% {
        transform: scale(0.2) rotate(900deg) translate(190px, 0px);
    }
    100% {
        transform: scale(0.05) translate(0px, 0px);
    }
}

@keyframes loader-anim-blue {
    0% {
        transform: scale(0.2) rotate(0deg) translate(170px, 0px);
    }
    95% {
        transform: scale(0.2) rotate(1080deg) translate(190px, 0px);
    }
    100% {
        transform: scale(0.05) translate(0px, 0px);
    }
}

@keyframes loader-anim-orange {
    0%, 70% {
        transform: scale(0.2) rotate(30deg) translate(0px);
    }
    85% {
        transform: scale(0.2) rotate(30deg) translate(-300px);
    }
    100% {
        transform: scale(0.2) rotate(30deg) translate(0px);
    }
}

@keyframes wrapper-loader-anim {
    0% {
        transform: scale(0) translateY(0px);
    }
    10% {
        transform: scale(1) translateY(20px);
    }
    90% {
        transform: scale(1) translateY(-10px);
    }
    100% {
        transform: scale(0) translateY(0px);
    }
}

@keyframes checkmark {
    0% {
        height: 0; 
        width: 0;
        opacity: 0; 
    }
    90% {
        height: 0; 
        width: 0;
        opacity: 0; 
    }
    91% { 
        height: 0;
        width: 2px;
        opacity: 1;
    }
    92% { 
        height: 0;
        width: 4px;
        opacity: 1;
    }
    93% { 
        height: 0;
        width: 6px;
        opacity: 1;
    }
    94% { 
        height: 0;
        width: 8px;
        opacity: 1;
    }
    95% { 
        height: 0;
        width: 10px;
        opacity: 1;
    }
    96% { 
        height: 4px;
        width: 10px;
        opacity: 1;
    }
    97% { 
        height: 8px;
        width: 10px;
        opacity: 1;
    }
    98% { 
        height: 12px;
        width: 10px;
        opacity: 1;
    }
    99% { 
        height: 16px;
        width: 10px;
        opacity: 1;
    }
    100% {
        height: 18px;
        width: 10px;
        opacity: 1;
    }
  }