.events-container {
    margin: 10px;
}

.events-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(90deg, var(--violet), var(--blue));
    color: white;
    border-radius: 3px;
    padding: 5px 1em;
    margin-bottom: 1em;
}

.groups-cnt {
    display: flex;
    /* justify-content: space-evenly; */
}

.groups-cnt p {
    font-size: 1.6em;
}

.groups-cnt .violet {
    color: var(--violet);
    display: block;
    width: 300px;
    text-align: center;
    margin-left: .5em;
}

.groups-cnt .blue {
    color: var(--blue);
    display: block;
    width: 300px;
    text-align: center;
    margin-right: .5em;
}

.ev {
    display: flex;
    align-items: flex-end;
    margin: 2em 2em 0 2em;
    /* justify-content: space-between; */
}

.ev .time {
    font-size: 1.75em;
}

.ev-cards {
    flex: auto;
    display: flex;
    justify-content: space-evenly;
    color: #fff;
}

.ev .card {
    padding: .5em;
    width: 300px;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
}

.ev .ev-violet {
    background: var(--violet);
}

.ev .ev-blue {
    background: var(--blue);
}